/* 요소 외부 폰트 모음 */
@import url('https://fonts.googleapis.com/css2?family=Black+Han+Sans&family=Nanum+Myeongjo&family=Noto+Sans+KR:wght@900&family=Noto+Serif+KR:wght@500&display=swap');

.sign_up {
  color: var(--bc-1, #062C7F);
  font-family: Pretendard;
  font-size: 36px;
  font-style: normal;
  font-weight: 600;
  line-height: 48px;
  /* 133.333% */
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap:16px;
}

.body1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 280px;
  height: 384px;
  flex-shrink: 0;
  background: #ECECEC;
}

.agree{
  color: var(--bc-1, #062C7F);
font-family: Pretendard;
font-size: 28px;
font-style: normal;
font-weight: 600;
line-height: 40px; /* 142.857% */
}

.agree1{
  width:280px;
  color: #333;
font-family: Pretendard;
font-size: 16px;
font-style: normal;
font-weight: 500;
line-height: 24px; /* 150% */
}

.form1{
  display: flex;
  flex-direction: column;
  gap:16px;
  width:280px;
  appearance: none;
}

.check{
  display:none;
}

.agree_div {
  overflow:auto;

  width: 280px;
  height: 216px;

  color: #333;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  background-color:#ECECEC;;
  /* 133.333% */
}

.next_btn_o {
  width: 280px;
  height: 40px;
  background: var(--bc-2, #0A559F);

  color: rgba(255, 255, 255, 0.90);
  font-family: Pretendard;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 36px;
  /* 150% */
}

.next_btn_x {
  width: 280px;
  height: 40px;
  background: #999;

  color: rgba(255, 255, 255, 0.90);
  font-family: Pretendard;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 36px;
  /* 150% */
}